import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import ReactMarkdown from "react-markdown";

import '../Products/Products.scss'

export const Products = () => {

    const [products, setProduct] = useState()
    const [test, setTest] = useState()

    const fetchData = async () => {
      try {
        const res = await axios.get("http://localhost:1337/api/products")
        setProduct(res.data.data)
        
        const res2 = await axios.get("http://localhost:1337/api/tests")
        setTest(res2.data.data)
          
      } catch (error) {
        console.log(error)
      }
    }
    useEffect(() => {
      fetchData()
    }, [])

    return (
        <div className='products'>           
            {test?.map((t) => {
                return(
                    <ReactMarkdown className={t.attributes.text} key={t.id}>{t.attributes.text}</ReactMarkdown>
                )
            })}<br/>
            {products?.map((product) => {
                return(
                    <Link className='prodLink' key={product.id} to={`/product/${product.id}`}>                    
                        <div className='product-info' key={product.id}>
                            <h3 className='prodTitle'>{product.attributes.title}</h3>
                            <p><b>${product.attributes.price}</b></p>
                        </div>
                    </Link>
                )
            })}
        </div>
    )
}
