import React, {useEffect, useState} from 'react'
import { useParams } from 'react-router-dom'
import axios from 'axios'
import '../Product/Product.scss'
import { useDispatch } from 'react-redux'
import { addToCart } from '../../redux/cartReducer'
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";

export const Product = () => {

    const dispatch = useDispatch()

    const [quantity, setQuantity] = useState(1)
    const [product, setProduct] = useState()
    const {id} = useParams()
    
    const fetchData = async () => {
        try {
            const res = await axios.get(`http://localhost:1337/api/products/${id}?populate=*`)
            setProduct(res.data.data)
            console.log(res.data.data)
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        fetchData()
    }, [])

  return (
    <div className='container'>
        {product &&
            <div className='product'>
                <h2 className='title'>{product.attributes.title}</h2>
                <img className='img' src={`http://localhost:1337${product.attributes.image.data.attributes.formats.thumbnail.url}`}/>
                <div className='quantity'>
                    <button
                        onClick={() =>
                        setQuantity((prev) => (prev === 1 ? 1 : prev - 1))
                        }
                    >
                        -
                    </button>
                    {quantity}
                    <button onClick={() => setQuantity((prev) => prev + 1)}>+</button>
                </div>
                {/* <p>Category: <b>{product.attributes.categories.data[0].attributes.name}</b></p> */}
                <p className='description'>{product.attributes.description}</p>
                <p><b>${product.attributes.price}</b></p>

                <AddShoppingCartIcon className='button' onClick={()=> dispatch(addToCart({
                    id: product.id,
                    title: product.attributes.title,
                    desc: product.attributes.description,
                    price: product.attributes.price,
                    img: product.attributes.image.data.attributes.formats.thumbnail.url,
                    quantity,
                }))}/>
            </div>
        }
    </div>
  )
}
