import React, { useEffect, useState } from 'react'
import ReactMarkdown from "react-markdown";

import '../Stories/Stories.scss'

import { useQuery,gql } from "@apollo/client"

const HERO = gql`
  query{
    story(id: 6){
      data{
        attributes{
          blocks{
            block_type
            text{
              text
            }
            image{
              image{
                data{
                  attributes{
                    url
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`

export const Stories = () => {
    const [blocks, setBlocks] = useState()
    
    const { loading, error, data } = useQuery(HERO)
    
    useEffect(() => {
      const fetchBlocks = async () => {
        try {
          data.story.data.attributes.blocks && setBlocks(data.story.data.attributes.blocks)
          
        } catch (error) {
          console.log(error)
        }
      }
      fetchBlocks()
    
    }, [data])
    {blocks?.map((b) => {
        // b.text.map((t) => {
            console.log(b.isHero)
        // })
    })}
    return (
        <div className='story'>
            {blocks?.map((b, index) => {
            return(
                
                b.block_type === 'hero_with_text_bttm' && 
                    <div className='hero_block' key={index}>
                        <img className='hero_img' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                        <ReactMarkdown className='hero_text_bttm'>{b.text[0].text}</ReactMarkdown> 
                    </div> 
                    
                    ||
                
                b.block_type === 'hero_with_text_top' && 
                    <div className='hero_block' key={index}>
                        <img className='hero_img' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                        <ReactMarkdown className='hero_text_top'>{b.text[0].text}</ReactMarkdown> 
                    </div> 
                    
                    ||

                b.block_type === 'double_img' &&
                    <div className='block' key={index}>
                        <img className='block_left' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                        <img className='block_right' src={`http://localhost:1337${b.image[1].image.data.attributes.url}` } />
                    </div>

                    ||

                b.block_type === 'double_text' && 
                    <div className='block' key={index}>
                        <ReactMarkdown className='block_left'>{b.text[0].text}</ReactMarkdown> 
                        <ReactMarkdown className='block_right'>{b.text[1].text}</ReactMarkdown> 
                    </div>

                    ||

                b.block_type === 'single_img' &&
                    <div className='block' key={index}>
                        <img className='block_single' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                    </div>

                    ||

                b.block_type === 'single_text' &&
                    <div className='block' key={index}>
                        <ReactMarkdown className='block_single'>{b.text[0].text}</ReactMarkdown> 
                        {/* <ReactMarkdown className='block_right'>{b.text[1].text}</ReactMarkdown>  */}
                    </div>

                    ||

                b.block_type === 'text_right_img_left' &&
                    <div className='block' key={index}>
                        <ReactMarkdown className='block_right'>{b.text[0].text}</ReactMarkdown> 
                        <img className='block_left' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                    </div>
                    ||
                    
                b.block_type === 'text_left_img_right' &&
                    <div className='block' key={index}>
                        <ReactMarkdown className='block_left'>{b.text[0].text}</ReactMarkdown> 
                        <img className='block_right' src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                    </div>
                
        

                
                // b.block_type === ''
                // <div className={b.isHero ? 'hero_block' : 'block' } key={index}>
                //     {b.image[0] && 
                //         <img className={b.image[0].class} src={`http://localhost:1337${b.image[0].image.data.attributes.url}` } />
                //     }
                //     {b.image[1] && 
                //         <img className={b.image[1].class} src={`http://localhost:1337${b.image[1].image.data.attributes.url}` } />
                //     }
                //     {b.text[0] && 
                //        <ReactMarkdown className={b.text[0].class}>{b.text[0].text}</ReactMarkdown> 
                //     }
                //     {b.text[1] && 
                //         <ReactMarkdown  className={b.text[1].class}>{b.text[1].text}</ReactMarkdown> 
                //     }
                // </div>
            )
              
            })}
            
            
        </div>
    )
}
