import React from "react";
import "./Cart.scss";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import { useSelector } from "react-redux";
import { removeItem, resetCart } from "../../redux/cartReducer";
import { useDispatch } from "react-redux";
import { makeRequest } from "../../makeRequest";
import { loadStripe } from "@stripe/stripe-js";
import axios from "axios";
import { Buffer } from 'buffer';

const Cart = () => {
  const products = useSelector((state) => state.cart.products);
  const dispatch = useDispatch();
  const totalPrice = () => {
    let total = 0;
    products.forEach((item) => {
      total += item.quantity * item.price;
    });
    return total.toFixed(2);
  };

  const stripePromise = loadStripe(
    "pk_test_51MO9V9JmTVjhXnqYYHhPnlcJLHBdD5ttwsEmfTHn2TsmJ1a7MPlCNtUWmQIlbMbMnsejxTKgbAe3ehMRiu31LXl600GvWhbUS3"
  );
  const handlePayment = async (e) => {
    e.preventDefault()

    try {
      const stripe = await stripePromise;
      const res = await makeRequest.post("/checkout-session", {
        products
      });
      // console.log(res)
      return await stripe.redirectToCheckout({
        sessionId: await res.data.response.id,        
      });
      // return { res }
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <div className="cart">
      <h1>Products in your cart</h1>
      {products?.map((item) => (
        <div className="item" key={item.id}>
          <img src={`http://localhost:1337${item.img}`} alt="" />
          <div className="details">
            <h1>{item.title}</h1>
            <p>{item.desc?.substring(0, 45)}<span>...</span></p>
            <div className="price">
              {item.quantity} x ${item.price}
            </div>
          </div>
          <DeleteOutlinedIcon
            className="delete"
            onClick={() => dispatch(removeItem(item.id))}
          />
        </div>
      ))}
      <div className="total">
        <span>SUBTOTAL</span>
        <span>${totalPrice()}</span>
      </div>
      <button 
      onClick={((e)=> handlePayment(e))}
      // onClick={() => {
      //   // Send a post request to the Strapi route
      //   axios.post('/stripe/pay', {
      //     products
      //   })
      //     .then((response) => {
      //       // Your code to handle the response
      //     })
      //     .catch((error) => {
      //       // Your code to handle the error
      //     });
      // }}
      >PROCEED TO CHECKOUT</button>
      <span className="reset" onClick={() => dispatch(resetCart())}>
        Reset Cart
      </span>
    </div>
  );
};

export default Cart;